.elements {
    position: absolute;
    top: 0;
    right: calc((100vw - 1920px) / 2);
    bottom: 0;
    width: 1920px;
    margin: 0 auto;
    z-index: -1;
}

.elements_tablet {
    display: none;
}

.element {
    font-size: 60px;
    font-weight: bold;
    position: absolute;
    white-space: nowrap;
    top: 0;
    left: 0;
    opacity: 0;
}

.curly-brackets {
    transform: matrix(
        0.72159603080403,
        0.37432663296015,
        -0.37432663296015,
        0.72159603080403,
        0,
        0
    );
    animation: moveCurlyBrackets 2s 1s ease-out forwards;
}

@keyframes moveCurlyBrackets {
    to {
        opacity: 1;
        left: 123.127px;
        top: 57px;
    }
}

.hashtag {
    transform: matrix(
        0.91532249290851,
        0.26083794888422,
        -0.26083794888422,
        0.91532249290852,
        0,
        0
    );
    animation: moveHashtag 2s 1s ease-out forwards;
}

@keyframes moveHashtag {
    to {
        opacity: 1;
        left: 245.164px;
        top: 292px;
    }
}

.brackets-with-slash {
    transform: matrix(
        0.62913254694498,
        -0.25657231186215,
        0.25657231186215,
        0.62913254694498,
        0,
        0
    );
    animation: moveBracketsWithSlash 2s 1s ease-out forwards;
}

@keyframes moveBracketsWithSlash {
    to {
        opacity: 1;
        left: 363.726px;
        top: 590px;
    }
}

.bottom-brackets-with-slash {
    display: none;
}

.top-div-tag {
    transform: matrix(
        0.48280448948131,
        -0.12214047770298,
        0.12214047770298,
        0.48280448948131,
        0,
        0
    );
    animation: moveTopDivTag 2s 1s ease-out forwards;
}

@keyframes moveTopDivTag {
    to {
        opacity: 1;
        left: 752.412px;
        top: 80px;
    }
}

.bottom-div-tag {
    transform: matrix(
        0.58798214978162,
        0.21554688540119,
        -0.21554688540119,
        0.58798214978162,
        0,
        0
    );
    animation: moveBottomDivTag 2s 1s ease-out forwards;
}

@keyframes moveBottomDivTag {
    to {
        opacity: 1;
        left: 923.912px;
        top: 459px;
    }
}

